@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;400;700&display=swap);
.home .btn {
  padding: 0.4em 1.5em;
  font-size: 1.2em;
  border-radius: 7px;
}

.arrow {
  color: var(--black);
  opacity: 0.35;
  margin-left: 0.5em;
}

.home p {
  margin: 2em 0;
}

.home .btn:hover .arrow {
  -webkit-animation: stutter 0.5s ease-in infinite alternate;
          animation: stutter 0.5s ease-in infinite alternate;
}

@-webkit-keyframes stutter {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(80%);
  }
}

@keyframes stutter {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(80%);
  }
}

@media (min-width: 500px) {
  .home h1 {
    font-size: 3em;
  }
  .home p {
    font-size: 1.5em;
    margin: 2em 0;
  }
}

.product {
  display: flex;
  max-width: 300px;
  align-items: center;
  justify-content: space-between;
}

.product img {
  height: 100px;
  margin-right: 1em;
}

.product-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 100%;
}

.product-details h2 {
  font-size: 1em;
  width: 100%;
  align-self: flex-start;
}

.product-details form {
  display: flex;
  align-self: flex-end;
}

.product-details p {
  align-self: flex-end;
  line-height: 30px;
}

.product-details input[type='number'] {
  width: 40px;
  padding: 0.25em;
  margin-right: 0.5em;
  border: none;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.5);
}

.product-details input[type='number']:focus,
.product-details input[type='submit']:focus {
  outline: none;
}

.shop {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
}

.shop h1 {
  width: 100%;
}

.about h1 {
  font-size: 2em;
  margin-bottom: 0.25em;
  word-break: break-all;
}

.about h2 {
  font-size: 1.25em;
  margin: 0.25em 0;
  word-break: break-all;
}

.about i {
  margin-right: 0.25em;
}

.about p {
  font-size: 0.9em;
  word-break: break-all;
}

.inline {
  display: flex;
  align-items: center;
}

@media (min-width: 550px) {
  .about h1 {
    margin-bottom: 0.5em;
  }

  .about h2 {
    margin: 0.5em 0;
  }

  .about i {
    margin-right: 0.5em;
  }

  .about p {
    font-size: 1.2em;
  }
}

.cart {
  display: flex;
  flex-direction: column;
}

.cart h1 {
  font-size: 1.5em;
}

.cart table {
  width: 100%;
  border-collapse: collapse;
  margin: 1em 0;
}

.cart th {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0.5em 0;
}

.cart td {
  padding: 0.5em 0;
  text-align: center;
  font-size: 0.8em;
  max-width: 56px;
  word-break: break-all;
}

.cart input[type='number'] {
  max-width: 70%;
  border: none;
  padding: 0.25em;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.6);
}

.cart input[type='number']:focus {
  outline: none;
}

.total {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.total .btn {
  margin-top: 1em;
}

@media (min-width: 500px) {
  .total {
    flex-direction: row;
    align-self: flex-end;
  }

  .total .btn {
    margin-top: 0;
    margin-left: 1em;
  }

  .cart td,
  .cart th {
    text-align: left;
  }
}

.notification {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(118, 255, 118, 0.5);
  padding: 1em;
  text-align: center;
  border-radius: 10px;
}

* {
  font-family: 'Inter', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

:root {
  --primary: #efb17b;
  --tertiary: #cc8b64;
  --black: #000000;
  --white: #fff9f2;
}

body {
  background-image: url('https://images.unsplash.com/photo-1526894198609-10b3cdf45c52?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&w=3349');
  background-size: cover;
  background-repeat: no-repeat;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 100vh;
  color: #fff9f2;
  color: var(--white);
}

ul {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  width: 60%;
}

a {
  color: #fff9f2;
  color: var(--white);
  text-decoration: none;
  transition: color 0.2s ease-in;
}

li a:hover,
li a.active {
  color: #efb17b;
  color: var(--primary);
}

.container {
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1em;
  margin-bottom: 8em;
}

.nav-home {
  font-size: 1.2em;
  color: #efb17b;
  color: var(--primary);
}

.card {
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  color: #000000;
  color: var(--black);
  padding: 2em;
  border-radius: 20px;
  box-shadow: 0 0 20px 20px rgba(0, 0, 0, 0.2);
}

.btn {
  border: none;
  background-image: linear-gradient(to bottom, #cc8b64, #efb17b);
  background-image: linear-gradient(to bottom, var(--tertiary), var(--primary));
  color: #312219;
  padding: 0.25em 0.5em;
  font-size: 1.1em;
  cursor: pointer;
  border-radius: 7px;
  box-shadow: 0 0 10px 0 var(--shadow);
  transition: -webkit-filter 0.2s ease-in;
  transition: filter 0.2s ease-in;
  transition: filter 0.2s ease-in, -webkit-filter 0.2s ease-in;
  display: inline-flex;
  align-items: center;
}

.btn:focus {
  outline: none;
}

.btn:hover {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}

.cart-products {
  display: flex;
  align-items: center;
}

.cart-products i {
  margin-right: 0.25em;
}

@media (min-width: 500px) {
  body {
    background-position: center center;
  }

  .navigation {
    font-size: 1.3em;
    padding-top: 2em;
    margin-bottom: 4em;
  }

  ul {
    width: 30%;
  }
}

